import React, { useEffect } from "react";
import styles from "../Styles";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getSovereignNFT } from "../../Features/nftSlice";

const NFTGallery = () => {
  const dispatch = useDispatch();
  const { NFTEntities, loading } = useSelector((state) => state.NFT);
  const navigate = useNavigate();

  useEffect(() => {
    dispatch(getSovereignNFT());
    // eslint-disable-next-line
  }, []);

  if (loading) return <p>Loading...</p>;

  // ADD To Upgrade
  const upgradeNFT = (data) => {
    localStorage.setItem("NFTDetail", JSON.stringify(data));
    navigate("/upgradeNFT");
  };
  return (
    <>
      <div className={`${styles.headerContainer}`}>
        <div className="grid md:grid-cols-2 xl:grid-cols-4 gap-4">
          {NFTEntities?.map(
            (d) =>
              d?.collectionName === "Sovereign" && (
                <div
                  className="max-w-[360px] h-[450px] rounded-lg relative"
                  key={d.tokenAddress}
                >
                  <div>
                    <img
                      src={d.imageUrl}
                      alt={d.name}
                      className="rounded-lg h-[450px] w-full"
                    />
                  </div>
                  <div className="nft-card-filter absolute w-full bottom-0 p-6">
                    <div className="flex justify-between items-center">
                      <div>
                        <h1 className="nft-name text-base">{d.name}</h1>
                      </div>
                      <div>
                        <button
                          className="nft-upgrade-button"
                          onClick={() => upgradeNFT(d)}
                        >
                          Add to upgrade
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              )
          )}
        </div>
      </div>
    </>
  );
};

export default NFTGallery;
